<template>
  <div id="app">
    <nav>
      <!-- Navigation Links -->
      <router-link v-if="isLoggedIn" to="/events">Events    </router-link>
      <button v-if="isLoggedIn" @click="logout">Logout</button> <!-- Logout Button -->
    </nav>

    <!-- Routed components will be displayed here -->
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    // Check if the user is logged in by checking the presence of the token in localStorage
    isLoggedIn() {
      return !!localStorage.getItem('token');
    }
  },
  methods: {
    // Logout method to clear token and redirect to login
    logout() {
      localStorage.removeItem('token');

      // Optionally clear other stored user data
      localStorage.removeItem('userEmail');
      localStorage.removeItem('userId');

      // Redirect to the login page
      this.$router.push('/login');

      alert('You have successfully logged out!');
    }
  }
};
</script>
